.header{
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: var(--z-fixed);
    background-color: var(--body-color);
}

.nav{
    height: calc(var(--header-height) + 1.5rem);
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: 1rem;
}

.nav__logo,
.nav_toggle{
    color: var(--tittle-color);
    font-weight: var(--font-medium);
}

.nav__list{
    display: flex;
    column-gap: 2rem;
}

.nav__link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: var(--small-font-size);
    color: var(--tittle-color);
    font-weight: var(--font-medium);
    transition: .3s;
}

.nav__icon,
.nav__close,
.nav__toggle{
    display: none;
}

/* Active Link */
.active-link,
.nav__link:hover{
    color: black; 
}

.nav__link::after {
    content: '';
    display: block;
    background-color: black;
    height: 2px;
    width: 0;
    left: 0; /* Ajusta left a 0 para que la línea comience desde la izquierda */
    transition: width 0.3s;
}

.nav__link:hover::after {
    width: 100%;
}


/* BREAKPOINTS */
@media screen and (max-width: 768px){
    .header{
        top: initial;
        bottom: 0;
    }

    .nav{
        height: var(--header-height);
    }

    .nav__menu{
        position: fixed;
        bottom: -100%;
        left: 0;
        width: 100%;
        background-color: var(--body-color);
        padding: 2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0, 0, 0, 0.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition: .3s;
    }

    /* Show Menu */
    .show__menu{
        bottom: 0;
    }

    .nav__list{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 2rem;
    }

    .nav__icon{
        font-size: 1.2rem;
    }

    .nav__close{
        position: absolute;
        right: 1.3rem;
        bottom: .5rem;
        font-size: 1.5rem;
        cursor: pointer;
        color: var(--tittle-color);
    }

     .nav__close:hover{
        color: var(--tittle-color-dark);
     }

     .nav__toggle{
        font-size: 1.1rem;
        cursor: pointer;
     }

    .nav__icon,
    .nav__close,
    .nav__toggle{
        display: block;
    }
}

/* For small devices */
@media screen and (max-width: 350px){
    .nav__menu{
        padding: 2rem 0.25rem 4rem;
    }

    .nav__list{
        column-gap: 0;
    }
}